import { ref, onMounted } from "vue";
import axios from "axios";
import { useRouter } from "vue-router";
export default {
  setup() {
    const picturewidth = ref();
    picturewidth.value = 0.8 * window.innerWidth;
    const router = useRouter();
    const show = ref(false);
    const show_1 = ref(true);
    const picture = () => show.value = true;
    const onClickLeft = () => router.replace("/Produce");
    const imageUrl = ref("");
    const title = ref("");
    const desc = ref("");
    const credit = ref("");
    const num = ref("");
    const founderName = ref("");
    const useName = ref("");
    const registerTime = ref("");
    const updateTime = ref("");
    const token = localStorage.getItem("jwtToken"); // 从localStorage获取JWT令牌
    const resData = ref("");
    if (!token) {
      router.replace("/login");
    }
    const headers = {
      Authorization: `Bearer ${token}`
    };
    const fetchLoginToken = () => {
      axios.post("/api/loginToken?token=" + token).then(response => {
        if (response.data.code == 0) {
          console.error(response.data.data);
          router.replace("/login");
        }
        console.log(response.data.data);
        // 在这里处理登录令牌接口的响应
        // 如果需要执行一些特定的操作，可以在这里添加代码
      }).catch(error => {
        console.error("请求loginToken接口失败", error);
        router.replace("/login");
      });
    };
    const id = router.currentRoute.value.params.id;
    let strippedId = id.replace(":", "");
    const fetchDataAndFillForm = async () => {
      try {
        const response = await axios.get(`/api/idSelectProduce?id=${strippedId}`, {
          headers
        });
        resData.value = response.data.data;
        const data = response.data.data; // 假设服务器返回的数据是一个包含上述字段的对象

        // 填充表单字段
        imageUrl.value = data.produceImage;
        title.value = data.produceName;
        if (data.produceText == "") {
          desc.value = "😂这个人很懒，没有填写相关的商品详情！";
        } else desc.value = data.produceText;
        credit.value = data.produceCredit;
        num.value = data.produceNum;
        useName.value = data.customer;
        registerTime.value = data.registerTime;
        updateTime.value = data.updateTime;
        const founderId = data.founderId;
        const res = await axios.get(`/api/selectUser?id=${founderId}`, {
          headers
        });
        const resDate = res.data.data; // 假设服务器返回的数据是一个包含上述字段的对象
        founderName.value = resDate.name;
      } catch (error) {
        console.error("获取数据失败", error);
      }
      show_1.value = false;
    };
    // 在组件加载完成后自动触发数据加载和填充
    onMounted(() => {
      fetchDataAndFillForm();
      fetchLoginToken();
    });
    return {
      resData,
      picturewidth,
      show,
      show_1,
      picture,
      onClickLeft,
      imageUrl,
      title,
      desc,
      credit,
      num,
      founderName,
      useName,
      registerTime,
      updateTime,
      fetchDataAndFillForm
    };
  }
};